import react from "react";
import service1 from "../img/mantenimiento.svg";
import service2 from "../img/disign.svg";
import service3 from "../img/desarollo.svg";
import city from '../img/city.svg'
const Services = () => {
  return (
    <div className='mainServ'>
      <h1 className='titles s'>Mis Servicios</h1>

      <div class="services" id="services">
        <div className="card">
          <div className="img">
            <img src={service2} alt='img'/>
          </div>

          <div className="description">
            <h1> Desarollo web</h1>
            <p>
              Creare tu sitios web 100% personalizado, adaptable para cualquier             distositivo con las technologias del Stack MERN y librerias
            </p>
          </div>
        </div>


        <div className="card">
          <div className="img">
            <img src={service1} alt='img'/>
          </div>

          <div className="description">
            <h1> Desarollo Backend</h1>
            <p>
            Demosle un poco mas de interactividad a tu Aplicacion haciendola mas dinamica con bases de datos!Se puede agregar un panel administrativo para que tengas poder total sobre tu pagina
            </p>
          </div>
        </div>


        <div className="card">
          <div className="img">
            <img src={service3} alt='img'/>
          </div>

          <div className="description">
            <h1> Mantenimiento web</h1>
            <p>
       
ofrezco mantenimiento a tu pagina web Si esta escrita en los estándares web y con los lenguajes de programación web php, o JavaScript (MERN STACK)
            </p>
          </div>
        </div>
      </div>

    </div>
  );
};

export default react.memo(Services);
